<template>
  <div class="flex">
    <aside>
        <Sidebar :list="list" base="user" title="个人信息"/>
    </aside>
    <section>
        <router-view />
    </section>
  </div>
</template>

<script>
import Sidebar from "@/components/common/Sidebar";
export default {
  data() {
    return {
      list: [
        {
          txt: "基础信息",
          url: "base"
        },
        {
          txt: "修改密码",
          url: "pwd"
        },
        {
          txt: "申请为专家",
          url: "apply"
        },
        {
          txt: "人员审核",
          url: "review"
        },
        {
          txt: "权限管理",
          url: "auth"
        },
        {
          txt: "账户分配",
          url: "account"
        }
      ]
    };
  },

  components: {
    Sidebar
  },

  computed: {},

  mounted: function() {},

  methods: {}
};
</script>

<style lang='scss' scoped>
.flex {
  height: 100%;
}
aside {
  width: 260px;
}
section {
  width: calc(100% - 260px);
  background: #fff;
}
</style>



